@charset "UTF-8";
:root {
  --font-family: "Inter", sans-serif;
  --content-width: 100%;
  --container-offset: 2.5rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --dark-color: #2c2c2c;
  --brand-color: #ec1616;
  /* --brand-color: #7000FF; */
  --border-color: #E6E6E6;
  --gray-color: #f5f5f5;
  --size-xs: 0.625rem;
  --size-s: 1.25rem;
  --size-n: 1.875rem;
  --size-m: 2.5rem;
  --size-l: 3.75rem;
  --size-xxl: 8.75rem;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Overpass";
  src: url("../fonts/../fonts/Overpass-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: medium;
}
html {
  box-sizing: border-box;
  font-size: 1vw;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  width: 100%;
  border-bottom: 1px solid var(--border-color);
  background: var(--light-color);
  z-index: 999;
  position: fixed;
  top: 0;
  transition: transform 0.5s;
}
.header__logo {
  height: auto;
  width: 13rem;
}
.header__wrapper {
  padding: var(--size-xs) 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 50%;
}
.header__phone {
  margin-right: var(--size-m);
  color: var(--dark-color);
  display: flex;
  align-items: center;
}
.header__phone-icon {
  background: var(--gray-color);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 100%;
  margin-right: 15px;
}
.header__links {
  display: flex;
  align-items: center;
  justify-content: end;
  margin-left: var(--size-m);
}

.call-i {
  fill: none;
  stroke: var(--dark-color);
  stroke-width: 2px;
  height: 24px;
  width: 24px;
}

.header_hidden {
  transform: translateY(-100%);
}

@media (max-width: 576px) {
  .header__phone {
    margin-right: 0;
  }
  .header__phone-icon {
    margin-right: 0;
  }
  .header__wrapper {
    padding: 1rem 0;
  }
}
@media (max-width: 1024px) {
  .header__phone-num {
    display: none;
  }
}
.button-pr {
  padding: 1.3rem 2.6rem;
  background: var(--brand-color);
  color: var(--light-color);
  border-radius: 10rem;
}

.button-second {
  padding: 1.3rem 2.6rem;
  background: var(--brand-color);
  color: var(--light-color);
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;
}
@media (any-hover: hover) {
  .button-second:hover {
    background: var(--dark-color);
  }
}

.button-third {
  padding: 1.3rem 2.6rem;
  background: var(--gray-color);
  color: var(--dark-color);
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;
}
@media (any-hover: hover) {
  .button-third:hover {
    background: var(--dark-color);
    color: var(--light-color);
  }
  .button-third:hover .ic-download {
    stroke: var(--light-color);
  }
}

.link {
  color: var(--dark-color);
  transition: color 0.3s ease-in-out;
}

.section-head {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ptb-xxl {
  padding: var(--size-xxl) 0;
}

.section-title {
  margin: 0 0 var(--size-s) 0;
  font-size: 3.3rem;
  font-family: "Overpass";
  color: var(--dark-color);
}

.section-des {
  margin: 0 0 var(--size-l) 0;
  line-height: 140%;
  color: var(--dark-color);
}

.mark {
  color: var(--brand-color);
}

.mt-xxl {
  margin-top: var(--size-xxl);
}

.mb-xxl {
  margin-bottom: var(--size-xxl);
}

.mb-m {
  margin-bottom: var(--size-m);
}

.pt-xxl {
  padding-top: var(--size-xxl);
}

.hero__wrapper {
  margin: var(--size-xxl) 0;
  padding-top: 127px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__title {
  width: 40%;
}
.hero__des {
  width: 30%;
}
.hero__input-wrapper {
  padding: 2px;
  display: flex;
  align-items: center;
  border-radius: 10rem;
  background: var(--gray-color);
}
.hero__input {
  padding: 1.3rem 2.6rem;
  display: block;
  border-radius: 10rem;
  background: var(--gray-color);
}
.hero__img-wrapper {
  overflow: hidden;
  height: 70vh;
}
.hero__img-wrapper img {
  width: 100%;
}

.cards__title {
  width: 40%;
}
.cards__des {
  width: 30%;
}
.cards__grid {
  display: grid;
  grid-template-columns: 1.3fr 1fr;
  gap: var(--size-m);
}
.cards__grid--inv {
  grid-template-columns: 1fr 1.3fr;
}
.cards__el {
  padding: var(--size-m);
  height: 30rem;
  position: relative;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  justify-content: space-between;
  background: rgb(113, 113, 113);
  border-radius: var(--size-n);
  background-size: cover;
  background-repeat: no-repeat;
  color: var(--light-color);
}
.cards__el-title {
  font-family: "Overpass";
  width: 60%;
  font-size: 2rem;
}
.cards__el-list {
  width: 60%;
  line-height: 140%;
}
.cards__el-list li:not(:last-child) {
  margin-bottom: var(--size-m);
}
.cards__el-hide {
  padding: var(--size-m);
  width: 100%;
  height: 100%;
  top: 100%;
  left: 0;
  position: absolute;
  background: var(--brand-color);
  border-radius: inherit;
  transition: all 0.3s cubic-bezier(1, 0, 0, 1);
}
.cards__el-hide--active {
  top: 0;
}
.cards__el-control {
  display: flex;
  justify-content: end;
  z-index: 99;
}
.cards__el-btn {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 100%;
  transition: background 0.1s ease-in-out;
}
@media (any-hover: hover) {
  .cards__el-btn:hover {
    background: var(--light-color);
  }
  .cards__el-btn:hover .cross::after, .cards__el-btn:hover .cross::before {
    background: var(--brand-color);
  }
}
.cards .text {
  margin: 0;
  line-height: 140%;
  font-size: 1.5rem;
  height: 80%;
}

.cross {
  position: relative;
  height: 40px;
  width: 40px;
  transform: transform 0.1s ease-in-out;
}
.cross::after, .cross::before {
  content: "";
  position: absolute;
  background: var(--light-color);
  transition: background 0.1s ease-in-out;
}
.cross::after {
  height: 2px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
}
.cross::before {
  width: 2px;
  height: 100%;
  top: 0;
  left: 50%;
  margin-left: -1px;
}

.cross--active {
  transform: rotate(45deg);
}

.works {
  background: var(--gray-color);
}
.works__wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: var(--size-m);
  row-gap: var(--size-l);
  justify-items: center;
}
.works__inner {
  width: 80%;
}
.works__inner-number {
  color: var(--brand-color);
}
.works__inner-title {
  width: 60%;
  font-family: "Overpass";
  margin: var(--size-xs) 0 var(--size-s) 0;
  color: var(--dark-color);
}
.works__inner-des {
  margin: 0;
  line-height: 140%;
  color: var(--dark-color);
}

.doc-info {
  border-bottom: 1px solid var(--border-color);
}
.doc-info__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--size-m);
}
.doc-info__wrapper .section-title {
  width: 50%;
}
.doc-info__wrapper .section-des {
  width: 60%;
}
.doc-info__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.doc-info__img-wrapper img {
  width: 80%;
  height: auto;
}

.ic-download {
  fill: none;
  height: var(--size-n);
  width: var(--size-n);
  stroke: var(--dark-color);
  stroke-width: 2px;
}

.ic-download--inv {
  stroke: var(--light-color);
}

.link-title {
  display: flex;
  flex-direction: column;
  line-height: 120%;
  margin-left: var(--size-xs);
}
.link-title__sub {
  text-transform: uppercase;
  opacity: 0.5;
  font-size: 1rem;
}

.reviews__el {
  margin-right: var(--size-s);
  border: 1px solid var(--border-color);
  border-radius: var(--size-n);
}
.reviews__title {
  display: flex;
  align-items: center;
}
.reviews__img {
  height: 6rem;
  width: 6rem;
  display: block;
  background: #000;
  border-radius: 100%;
  margin-right: var(--size-s);
  background-size: cover;
  background-repeat: no-repeat;
}
.reviews__title {
  padding: var(--size-m) var(--size-m) 0 var(--size-m);
  color: var(--dark-color);
}
.reviews__head {
  display: flex;
  flex-direction: column;
}
.reviews__content {
  border-radius: 25px;
  padding: var(--size-m);
  background: var(--gray-color);
  line-height: 140%;
  margin: 15px;
  color: var(--dark-color);
}

.apartments__wrapper {
  background: #2C2C2C;
  border-radius: var(--size-n);
  overflow: hidden;
  padding: var(--size-xxl) var(--size-m) var(--size-m) var(--size-m);
}
.apartments__head {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.apartments .section-title,
.apartments .section-des {
  color: var(--light-color);
}
.apartments .section-des {
  width: 30%;
}
.apartments__el {
  margin-right: var(--size-s);
  overflow: hidden;
  border-radius: var(--size-n);
  background: var(--light-color);
}
.apartments__title {
  margin: var(--size-s);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  line-height: 140%;
  height: 8rem;
  color: var(--dark-color);
}

.room {
  width: 100%;
  position: relative;
}
.room img {
  width: 100%;
  height: auto;
}
.room .section-title {
  color: var(--light-color);
}
.room__head {
  width: 100%;
  margin-top: var(--size-xxl);
  display: flex;
  justify-content: center;
  position: absolute;
}
.room__wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
}

.price__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--size-m);
}
.price .button-second {
  margin-top: var(--size-m);
  width: 100%;
  text-align: center;
  justify-content: center;
}
.price__content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.price__row {
  display: flex;
  flex-direction: column;
}
.price__tax {
  color: var(--brand-color);
}
.price__head .section-des {
  width: 50%;
}

.calc {
  background: var(--gray-color);
  padding: var(--size-m);
  border-radius: var(--size-n);
  margin-right: var(--size-xxl);
}
.calc__head {
  margin-bottom: var(--size-l);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.calc__title {
  margin: 0;
  font-size: 2.5rem;
  color: var(--dark-color);
}
.calc__des {
  width: 50%;
  opacity: 0.5;
  text-align: center;
}
.calc__row {
  display: grid;
  grid-template-columns: 4fr 1fr;
  align-items: center;
  margin-top: var(--size-n);
  color: var(--dark-color);
}
.calc__inp-wrapper {
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: start;
  background: var(--light-color);
  border-radius: 10rem;
  width: auto;
  cursor: pointer;
}

#inp-0 {
  border: none;
  display: flex;
  width: 80px;
  color: var(--dark-color);
}
#inp-0::-webkit-outer-spin-button, #inp-0::-webkit-inner-spin-button {
  display: none;
}

.inp-edit-ic {
  height: var(--size-s);
  width: var(--size-s);
  display: block;
  fill: #000;
  opacity: 0.5;
  margin-right: 10px;
}

.slider__wrapper {
  margin-bottom: var(--size-l);
}

.noUi-tooltip {
  border: none;
  padding: 10px 20px;
  border-radius: 10rem;
  background: var(--light-color);
  color: var(--dark-color);
  bottom: 130% !important;
  box-shadow: 1px 0px 8px 0px rgba(0, 0, 0, 0.14);
}
.noUi-tooltip::after {
  content: "";
  bottom: -18px;
  left: 50%;
  margin-left: -10px;
  position: absolute;
  border: 10px solid transparent;
  border-top: 8px solid var(--light-color);
}

.noUi-horizontal {
  height: 2px;
  background: rgba(0, 0, 0, 0.1);
  border: none;
}

.noUi-horizontal .noUi-handle {
  top: -16px;
}

.noUi-handle {
  background: var(--brand-color);
  box-shadow: none;
  border: none;
  border-radius: 100%;
  width: 34px !important;
  height: 34px !important;
}
.noUi-handle::after, .noUi-handle::before {
  display: none;
}

.footer .section-title {
  margin-bottom: var(--size-m);
}
.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--size-m);
}
.footer .feedback-form {
  margin-right: var(--size-xxl);
}
.footer .feedback-form__row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: var(--size-m);
}
.footer__bot {
  margin-top: var(--size-xxl);
  padding: var(--size-m);
  background: var(--gray-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__bot span {
  text-transform: uppercase;
  color: var(--dark-color);
}
.footer__bot a {
  color: var(--dark-color);
  text-decoration: underline;
}
.footer__contact {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.footer__contact-title {
  font-size: 2rem;
  font-family: "Overpass";
  color: var(--dark-color);
}
.footer__links {
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  font-family: "Overpass";
  color: var(--dark-color);
  margin-bottom: var(--size-m);
}
.footer__links::before {
  opacity: 0.5;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.footer__phone::before {
  content: "Телефон";
}
.footer__mail {
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.footer__mail::before {
  content: "Почта";
}
.footer__soc a {
  padding: var(--size-s);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  background: var(--gray-color);
}
.footer__soc svg {
  fill: var(--brand-color);
  height: var(--size-s);
  width: var(--size-s);
}

.graph-modal__container {
  padding: var(--size-m);
  border-radius: var(--size-n);
  max-width: 600px;
}

.feedback-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.feedback-form__title {
  margin-bottom: var(--size-n);
  font-size: 1.5rem;
  font-family: "Overpass";
  text-align: center;
  color: var(--dark-color);
}
.feedback-form__inp, .feedback-form__area {
  width: 100%;
  padding: var(--size-n);
  margin-bottom: var(--size-xs);
  display: flex;
  background: var(--gray-color);
  border-radius: 20px;
}
.feedback-form__row {
  margin-top: var(--size-s);
  display: flex;
  flex-direction: column;
}
.feedback-form__politics {
  display: grid;
  align-items: start;
  grid-template-columns: 30px 1fr;
}
.feedback-form__politics label {
  margin-bottom: var(--size-n);
  font-size: 1rem !important;
  color: var(--dark-color);
}
.feedback-form__checkbox {
  margin-right: var(--size-xs);
  height: 20px;
  width: 40px;
  position: relative;
  display: flex;
}
.feedback-form__checkbox::before {
  content: "";
  position: absolute;
}
.feedback-form__politic-link {
  color: var(--dark-color);
  text-decoration: underline;
}
.feedback-form .button-second {
  justify-content: center;
}
.feedback-form input[type=checkbox] {
  --active: var(--brand-color);
  --active-inner: #fff;
  --focus: 0px rgba(0, 0, 0, .3);
  --border: #d4d4d4;
  --border-hover: var(--brand-color);
  --background: #fff;
  --disabled: #F6F8FF;
  --disabled-inner: #E1E6F9;
  -webkit-appearance: none;
  -moz-appearance: none;
  height: 21px;
  outline: none;
  display: inline-block;
  vertical-align: top;
  position: relative;
  cursor: pointer;
  border: 1px solid var(--bc, var(--border));
  background: var(--b, var(--background));
  transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
}
.feedback-form input[type=checkbox]:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  position: absolute;
  transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
}
.feedback-form input[type=checkbox]:checked {
  --b: var(--active);
  --bc: var(--active);
  --d-o: .3s;
  --d-t: .6s;
  --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
}
.feedback-form input[type=checkbox]:disabled {
  --b: var(--disabled);
  cursor: not-allowed;
  opacity: 0.9;
}
.feedback-form input[type=checkbox]:disabled:checked {
  --b: var(--disabled-inner);
  --bc: var(--border);
}
.feedback-form input[type=checkbox]:disabled + label {
  cursor: not-allowed;
}
.feedback-form input[type=checkbox]:hover:not(:checked):not(:disabled) {
  --bc: var(--border-hover);
}
.feedback-form input[type=checkbox]:focus {
  box-shadow: 0 0 0 var(--focus);
}
.feedback-form input[type=checkbox]:not(.switch) {
  width: 21px;
}
.feedback-form input[type=checkbox]:not(.switch):after {
  opacity: var(--o, 0);
}
.feedback-form input[type=checkbox]:not(.switch):checked {
  --o: 1;
}
.feedback-form input[type=checkbox] + label {
  font-size: 1.4rem;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  margin-left: 4px;
}
.feedback-form input[type=checkbox]:not(.switch) {
  border-radius: 7px;
}
.feedback-form input[type=checkbox]:not(.switch):after {
  width: 5px;
  height: 9px;
  border: 2px solid var(--active-inner);
  border-top: 0;
  border-left: 0;
  left: 7px;
  top: 4px;
  transform: rotate(var(--r, 20deg));
}
.feedback-form input[type=checkbox]:not(.switch):checked {
  --r: 43deg;
}
.feedback-form input[type=checkbox].switch {
  width: 38px;
  border-radius: 11px;
}
.feedback-form input[type=checkbox].switch:after {
  left: 2px;
  top: 2px;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background: var(--ab, var(--border));
  transform: translateX(var(--x, 0));
}
.feedback-form input[type=checkbox].switch:checked {
  --ab: var(--active-inner);
  --x: 17px;
}
.feedback-form input[type=checkbox].switch:disabled:not(:checked):after {
  opacity: 0.6;
}

.graph-modal__close {
  right: 20px;
  top: 20px;
}
.graph-modal__close .cross {
  position: static;
}
.graph-modal__close .cross::after,
.graph-modal__close .cross::before {
  transform: rotate(45deg);
  background: var(--dark-color);
  opacity: 0.5;
}
.graph-modal__close:hover .cross::after,
.graph-modal__close:hover .cross::before {
  background: var(--brand-color);
  opacity: 1;
}

.swiper-button-next,
.swiper-button-prev {
  color: var(--brand-color);
}

.feedback-mob-button {
  bottom: 0;
  width: 100%;
  padding: 1.5rem 3rem;
  z-index: 99;
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  background: var(--light-color);
  color: var(--brand-color);
  border-top: 1px solid var(--border-color);
  /*   box-shadow: 0px -7px 20px 0px rgba(0, 0, 0, 0.1); */
}

.send-ic {
  height: 30px;
  width: 30px;
  margin-left: 10px;
  stroke-width: 1px;
  stroke: var(--brand-color);
  fill: none;
}

