  }
  .works__wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
  .cards .container {
    padding: 0;
  }
  .cards__title, .cards__des {
    padding: 0 var(--size-m);
  }
  .cards__grid {
    grid-template-columns: 1fr;
    gap: 0;
