.button-pr {
  padding: 1.3rem 2.6rem;
  background: var(--brand-color);
  color: var(--light-color);
  border-radius: 10rem;
}

.button-second {
  padding: 1.3rem 2.6rem;
  background: var(--brand-color);
  color: var(--light-color);
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;


  @media (any-hover: hover) {
    &:hover {
      background: var(--dark-color);
    }
  }
}

.button-third {
  padding: 1.3rem 2.6rem;
  background: var(--gray-color);
  color: var(--dark-color);
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;

  @media (any-hover: hover) {
    &:hover {
      background: var(--dark-color);
      color: var(--light-color);

      .ic-download {
        stroke: var(--light-color);
      }
    }
  }
}

.link {
  color: var(--dark-color);
  transition: color .3s ease-in-out;
}

.section-head {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ptb-xxl {
  padding: var(--size-xxl) 0;
}

.section-title {
  margin: 0 0 var(--size-s) 0;
  font-size: 3.3rem;
  font-family: "Overpass";
  color: var(--dark-color);
}

.section-des {
  margin: 0 0 var(--size-l) 0;
  line-height: 140%;
  color: var(--dark-color);
}

.mark {
  color: var(--brand-color);
}

.mt-xxl {
  margin-top: var(--size-xxl);
}

.mb-xxl {
  margin-bottom: var(--size-xxl);
}

.mb-m {
  margin-bottom: var(--size-m);
}

.pt-xxl {
  padding-top: var(--size-xxl);
}


// hero

.hero {

  &__wrapper {
    margin: var(--size-xxl) 0;
    padding-top: 127px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }


  &__title {
    width: 40%;
  }

  &__des {
    width: 30%;
  }

  &__input-wrapper {
    padding: 2px;
    display: flex;
    align-items: center;
    border-radius: 10rem;
    background: var(--gray-color);
  }

  &__input {
    padding: 1.3rem 2.6rem;
    display: block;
    border-radius: 10rem;
    background: var(--gray-color);
  }

  &__img-wrapper {
    overflow: hidden;
    height: 70vh;

    img {
      width: 100%;
    }
  }
}


// Карточки
.cards {
  &__title {
    width: 40%;
  }

  &__des {
    width: 30%;
  }

  &__grid {
    display: grid;
    grid-template-columns: 1.3fr 1fr;
    gap: var(--size-m);
  }

  &__grid--inv {
    grid-template-columns: 1fr 1.3fr;
  }

  &__el {
    padding: var(--size-m);
    height: 30rem;
    position: relative;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    justify-content: space-between;
    background: rgb(113, 113, 113);
    border-radius: var(--size-n);
    background-size: cover;
    background-repeat: no-repeat;
    color: var(--light-color);

  }

  &__el-title {
    font-family: "Overpass";
    width: 60%;
    font-size: 2rem;
  }

  &__el-list {
    width: 60%;
    line-height: 140%;

    li:not(:last-child) {
      margin-bottom: var(--size-m);
    }
  }

  &__el-hide {
    padding: var(--size-m);
    width: 100%;
    height: 100%;
    top: 100%;
    left: 0;
    position: absolute;
    background: var(--brand-color);
    border-radius: inherit;
    transition: all .3s cubic-bezier(1, 0, 0, 1);
  }

  &__el-hide--active {
    top: 0;
  }

  &__el-control {
    display: flex;
    justify-content: end;
    z-index: 99;
  }

  &__el-btn {
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 100%;
    transition: background .1s ease-in-out;


    @media (any-hover: hover) {
      &:hover {
        background: var(--light-color);

        .cross {

          &::after,
          &::before {
            background: var(--brand-color);
          }
        }
      }
    }
  }

  .text {
    margin: 0;
    line-height: 140%;
    font-size: 1.5rem;
    height: 80%;
  }
}

.cross {
  position: relative;
  height: 40px;
  width: 40px;
  transform: transform .1s ease-in-out;

  &::after,
  &::before {
    content: '';
    position: absolute;
    background: var(--light-color);
    transition: background .1s ease-in-out;
  }

  &::after {
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
  }

  &::before {
    width: 2px;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -1px;

  }

}

.cross--active {
  transform: rotate(45deg);
}

// Как мы работаем
.works {
  background: var(--gray-color);

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: var(--size-m);
    row-gap: var(--size-l);
    justify-items: center;
  }

  &__inner {
    width: 80%;
  }

  &__inner-number {
    color: var(--brand-color);
  }

  &__inner-title {
    width: 60%;
    font-family: "Overpass";
    margin: var(--size-xs) 0 var(--size-s) 0;
    color: var(--dark-color);
  }

  &__inner-des {
    margin: 0;
    line-height: 140%;
    color: var(--dark-color);
  }
}

// doc

.doc-info {
  border-bottom: 1px solid var(--border-color);

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--size-m);

    .section-title {
      width: 50%;
    }

    .section-des {
      width: 60%;

    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__img-wrapper {
    img {
      width: 80%;
      height: auto;
    }
  }
}

.ic-download {
  fill: none;
  height: var(--size-n);
  width: var(--size-n);
  stroke: var(--dark-color);
  stroke-width: 2px;
}

.ic-download--inv {
  stroke: var(--light-color);
}

.link-title {
  display: flex;
  flex-direction: column;
  line-height: 120%;
  margin-left: var(--size-xs);

  &__sub {
    text-transform: uppercase;
    opacity: .5;
    font-size: 1rem;
  }
}


// Отзывы
.reviews {
  &__el {
    margin-right: var(--size-s);
    border: 1px solid var(--border-color);
    border-radius: var(--size-n);
  }

  &__title {
    display: flex;
    align-items: center;
  }

  &__img {
    height: 6rem;
    width: 6rem;
    display: block;
    background: #000;
    border-radius: 100%;
    margin-right: var(--size-s);
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__title {
    padding: var(--size-m) var(--size-m) 0 var(--size-m);
    color: var(--dark-color);
  }

  &__head {
    display: flex;
    flex-direction: column;
  }

  &__content {
    border-radius: 25px;
    padding: var(--size-m);
    background: var(--gray-color);
    line-height: 140%;
    margin: 15px;
    color: var(--dark-color);
  }
}


.apartments {
  &__wrapper {
    background: #2C2C2C;
    border-radius: var(--size-n);
    overflow: hidden;
    padding: var(--size-xxl) var(--size-m) var(--size-m) var(--size-m);
  }

  &__head {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .section-title,
  .section-des {
    color: var(--light-color);
  }

  .section-des {
    width: 30%;
  }

  &__el {
    margin-right: var(--size-s);
    overflow: hidden;
    border-radius: var(--size-n);
    background: var(--light-color);
  }

  &__title {
    margin: var(--size-s);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    line-height: 140%;
    height: 8rem;
    color: var(--dark-color);
  }
}

.room {
  width: 100%;
  position: relative;

  img {
    width: 100%;
    height: auto;
  }

  .section-title {
    color: var(--light-color);
  }

  &__head {
    width: 100%;
    margin-top: var(--size-xxl);
    display: flex;
    justify-content: center;
    position: absolute;
  }

  &__wrapper {
    position: absolute;
    height: 100%;
    width: 100%;
  }
}

// Калькулятор
.price {
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--size-m);
  }

  .button-second {
    margin-top: var(--size-m);
    width: 100%;
    text-align: center;
    justify-content: center;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  &__row {
    display: flex;
    flex-direction: column;
  }

  &__tax {
    color: var(--brand-color);
  }

  &__head {
    .section-des {
      width: 50%;
    }
  }
}

.calc {
  background: var(--gray-color);
  padding: var(--size-m);
  border-radius: var(--size-n);
  margin-right: var(--size-xxl);

  &__head {
    margin-bottom: var(--size-l);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    font-size: 2.5rem;
    color: var(--dark-color);
  }

  &__des {
    width: 50%;
    opacity: .5;
    text-align: center;
  }

  &__row {
    display: grid;
    grid-template-columns: 4fr 1fr;
    align-items: center;
    margin-top: var(--size-n);
    color: var(--dark-color);
  }

  &__inp-wrapper {
    padding: 10px 20px;
    display: flex;

    align-items: center;
    justify-content: start;
    background: var(--light-color);
    border-radius: 10rem;
    width: auto;
    cursor: pointer;
  }

}

#inp-0 {
  border: none;
  display: flex;
  width: 80px;
  color: var(--dark-color);

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    display: none;
  }
}

.inp-edit-ic {
  height: var(--size-s);
  width: var(--size-s);
  display: block;
  fill: #000;
  opacity: .5;
  margin-right: 10px;
}


.slider__wrapper {
  margin-bottom: var(--size-l);
}

.noUi-tooltip {
  border: none;
  padding: 10px 20px;
  border-radius: 10rem;
  background: var(--light-color);
  color: var(--dark-color);
  bottom: 130% !important;
  box-shadow: 1px 0px 8px 0px rgba(0, 0, 0, 0.14);

  &::after {
    content: '';
    bottom: -18px;
    left: 50%;
    margin-left: -10px;
    position: absolute;
    border: 10px solid transparent;
    border-top: 8px solid var(--light-color);
  }
}

.noUi-horizontal {
  height: 2px;
  background: rgba(0, 0, 0, 0.1);
  border: none;
}

.noUi-horizontal .noUi-handle {
  top: -16px;
}

.noUi-handle {
  background: var(--brand-color);
  box-shadow: none;
  border: none;
  border-radius: 100%;
  width: 34px !important;
  height: 34px !important;

  &::after,
  &::before {
