// Форма
.graph-modal__container {
  padding: var(--size-m);
  border-radius: var(--size-n);
  max-width: 600px;
}

.feedback-form {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    margin-bottom: var(--size-n);
    font-size: 1.5rem;
    font-family: "Overpass";
    text-align: center;
    color: var(--dark-color);
  }

  &__inp,
  &__area {
    width: 100%;
    padding: var(--size-n);
    margin-bottom: var(--size-xs);
    display: flex;
    background: var(--gray-color);
    border-radius: 20px;
  }

  &__row {
    margin-top: var(--size-s);
    display: flex;
    flex-direction: column;
  }

  &__politics {
    display: grid;
    align-items: start;
    grid-template-columns: 30px 1fr;


    label {
      margin-bottom: var(--size-n);
      font-size: 1rem !important;
      color: var(--dark-color);
    }
  }

  &__checkbox {
    margin-right: var(--size-xs);
    height: 20px;
    width: 40px;
    position: relative;
    display: flex;

    &::before {
      content: '';
      position: absolute;
    }
  }

  &__politic-link {
    color: var(--dark-color);
    text-decoration: underline;
  }

  .button-second {
    justify-content: center;
  }

  input[type='checkbox'] {
    --active: var(--brand-color);
    --active-inner: #fff;
    --focus: 0px rgba(0, 0, 0, .3);
    --border: #d4d4d4;
    --border-hover: var(--brand-color);
    --background: #fff;
    --disabled: #F6F8FF;
    --disabled-inner: #E1E6F9;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 21px;
    outline: none;
    display: inline-block;
    vertical-align: top;
    position: relative;
    cursor: pointer;
    border: 1px solid var(--bc, var(--border));
    background: var(--b, var(--background));
    transition: background .3s, border-color .3s, box-shadow .2s;

    &:after {
      content: '';
      display: block;
      left: 0;
      top: 0;
      position: absolute;
      transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
    }

    &:checked {
      --b: var(--active);
      --bc: var(--active);
      --d-o: .3s;
      --d-t: .6s;
      --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
    }

    &:disabled {
      --b: var(--disabled);
      cursor: not-allowed;
      opacity: .9;

      &:checked {
        --b: var(--disabled-inner);
        --bc: var(--border);
      }

      &+label {
        cursor: not-allowed;
      }
    }

    &:hover {
      &:not(:checked) {
        &:not(:disabled) {
          --bc: var(--border-hover);
        }
      }
    }

    &:focus {
      box-shadow: 0 0 0 var(--focus);
    }

    &:not(.switch) {
      width: 21px;

      &:after {
        opacity: var(--o, 0);
      }

      &:checked {
        --o: 1;
      }
    }

    &+label {
      font-size: 1.4rem;
      display: inline-block;
      vertical-align: top;
      cursor: pointer;
      margin-left: 4px;
    }
  }

  input[type='checkbox'] {
    &:not(.switch) {
      border-radius: 7px;

      &:after {
        width: 5px;
        height: 9px;
        border: 2px solid var(--active-inner);
        border-top: 0;
        border-left: 0;
        left: 7px;
        top: 4px;
        transform: rotate(var(--r, 20deg));
      }

      &:checked {
        --r: 43deg;
      }
    }

    &.switch {
      width: 38px;
      border-radius: 11px;

      &:after {
        left: 2px;
        top: 2px;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: var(--ab, var(--border));
        transform: translateX(var(--x, 0));
      }

      &:checked {
        --ab: var(--active-inner);
        --x: 17px;
      }

      &:disabled {
        &:not(:checked) {
          &:after {
            opacity: .6;
          }
        }
      }
    }
  }
}


.graph-modal__close {

  right: 20px;
  top: 20px;

  .cross {
    position: static;

  }

  .cross::after,
  .cross::before {
    transform: rotate(45deg);
    background: var(--dark-color);
    opacity: .5;
  }

  &:hover {

    .cross::after,
    .cross::before {
      background: var(--brand-color);
      opacity: 1;
    }
  }
}


.swiper-button-next,
.swiper-button-prev {
  color: var(--brand-color);
}

.feedback-mob-button {
  bottom: 0;
  width: 100%;
  padding: 1.5rem 3rem;
  z-index: 99;
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  background: var(--light-color);
  color: var(--brand-color);
  border-top: 1px solid var(--border-color);
  /*   box-shadow: 0px -7px 20px 0px rgba(0, 0, 0, 0.1); */
}

.send-ic {
  height: 30px;
  width: 30px;
  margin-left: 10px;
  stroke-width: 1px;
  stroke: var(--brand-color);
  fill: none;
}

@media (max-width: (1024px)) {
