    --container-offset: 1rem;
    --size-m: 1.5rem; // 40
    --size-xxl: 4.75rem; // 140
  }

  html {
    font-size: 1.05rem;
  }

  .section-title,
  .section-des {
    width: 80%;
  }

  .hero {}

  .works {
    margin-top: 0;
  }

  .works__wrapper {
    grid-template-columns: repeat(2, 1fr);
  }


  .cards {
    .container {
      padding: 0;
    }

    &__title,
    &__des {
      padding: 0 var(--size-m);
    }


    &__grid {
      grid-template-columns: 1fr;
      gap: 0;
      margin-bottom: 0;
    }

    &__el {
      border-radius: 0;
      margin: 0;
    }

    &__el-title,
    &__el-list {
      width: 100%;
    }

    &__el-hide {
      .text {
        font-size: 1.5rem;
      }

    }
  }

  .doc-info {

    &__wrapper {
      grid-template-columns: 2fr 1fr;
      align-self: center;
      align-items: center;
    }

  }

  .price {
    &__wrapper {
      grid-template-columns: 1.3fr 1fr;
      gap: var(--size-l);
    }
  }

  .calc {
    margin-right: 0;
  }

  .footer {
    .section-title {
      width: 100%;
    }

    .feedback-form__row {
      grid-template-columns: 1fr;
    }

    &__wrapper {
      gap: var(--size-l);

      .feedback-form {
        margin-right: 0;
      }
    }
  }
}

