    }

    &__links {
      a {
        width: 100%;
        justify-content: center;
      }

      a:not(:last-child) {
        margin-bottom: var(--size-xs);
      }
    }

    &__img-wrapper {
      img {
        width: 100%;
      }
    }
  }

  .ic-download {
    height: var(--size-n);
    width: var(--size-n);
  }

  .price {
    &__wrapper {
      display: flex;
      flex-direction: column-reverse;
    }

    .section-title,
    .section-des {
      text-align: center;
    }

    &__row {
      align-items: center;
    }
  }

  .calc {
    margin-right: 0;

    &__title {
      font-size: 1.8rem;
    }

    &__des {
      width: 100%;
    }

    &__row {
      grid-template-columns: 2fr 1fr;
    }
  }

  .reviews {
    &__el {
      margin-right: 0;
    }

    &__img {
      height: 4rem;
      width: 4rem;
    }

    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }

    .container {
      padding: 0 0 0 var(--size-m);
    }
  }

  .apartments {
    &__wrapper {
      border-radius: 0;
      padding-right: 0;
      padding-left: 0;
      padding-bottom: var(--size-xxl);

    }

    &__head {
      padding: 0 var(--size-m);
    }

    .container {
      padding: 0;
    }

    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }

    &__el {
      margin-right: 0;
    }
  }

  .footer {
    .feedback-form {
      margin-right: 0;

      label {
        margin-bottom: 0;
      }
    }

    .feedback-form__politics {
      label {
        font-size: .8rem;
      }
    }

    &__wrapper {
      grid-template-columns: 1fr;
    }

    .feedback-form__row {
      grid-template-columns: 1fr;
    }

    &__contact-title {
      font-size: 1.5rem;
      text-align: center;
      margin: var(--size-l) 0 var(--size-m) 0;
    }

    &__contact-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__links {
      text-align: center;
      font-size: 1.8rem;

      &::before {
        font-size: 1rem;
      }
    }

    &__soc {
      a {
        padding: var(--size-s);
      }
    }

    &__bot {
      flex-direction: column;
      line-height: 200%;
      padding-bottom: 100px;
    }
  }

  .graph-modal {
    .feedback-form__title {
      margin-top: var(--size-m);
    }
  }
}
