.header {
  width: 100%;
  border-bottom: 1px solid var(--border-color);
  background: var(--light-color);
  z-index: 999;
  position: fixed;
  top: 0;
  transition: transform .5s;

  &__logo {
    height: auto;
    width: 13rem;
  }

  &__wrapper {
    padding: var(--size-xs) 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 50%;
  }

  &__phone {
    margin-right: var(--size-m);
    color: var(--dark-color);
    display: flex;
    align-items: center;
  }

  &__phone-icon {
    background: var(--gray-color);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 100%;
    margin-right: 15px;
  }

  &__links {
    display: flex;
    align-items: center;
    justify-content: end;
    margin-left: var(--size-m);
  }

}


.call-i {
  fill: none;
  stroke: var(--dark-color);
  stroke-width: 2px;
  height: 24px;
  width: 24px;
}

.header_hidden {
  transform: translateY(-100%);
}

@media (max-width: (576px)) {
  .header {

    &__phone {
      margin-right: 0;
    }

    &__phone-icon {
      margin-right: 0;
    }
  }

  .header__wrapper {
    padding: 1rem 0;
  }

}

@media (max-width: (1024px)) {
  .header__phone-num {
    display: none;
  }

}
