// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Inter", sans-serif;
  --content-width: 100%;
  --container-offset: 2.5rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark-color: #2c2c2c;
  --brand-color: #ec1616;
  /* --brand-color: #7000FF; */
  --border-color: #E6E6E6;
  --gray-color: #f5f5f5;

  --size-xs: 0.625rem; // 10
  --size-s: 1.25rem; // 20
  --size-n: 1.875rem; // 30
  --size-m: 2.5rem; // 40
  --size-l: 3.75rem; // 60
  --size-xxl: 8.75rem; // 140
}
